@import "variables";
@import "custom";
@import "material-input";
@import "breakpoints";
@import "utilities";
@import "components";
@import "hamburger-menu";

/* --------------------------------------------------------------------------
1. Main Hero
 -------------------------------------------------------------------------- */

/* Not found page */
.not-found {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	text-align: center;

	h2 {
		font-family: "Playfair Display", serif;
	}
	ul {
		padding: 0;
		margin-top: 24px;
		list-style-type: none;
		display: flex;
		flex-direction: column;
		gap: 16px;

		a {
			text-transform: capitalize;
			position: relative;

			&::before {
				content: "";
				position: absolute;
				left: 0;
				bottom: -5px;
				height: 1px;
				width: 0;
				background-color: black;
				transition: 300ms;
			}

			&:hover {
				&::before {
					transition: 300ms;
					width: 100%;
				}
			}
		}
	}
}

/* Preload */
@keyframes float {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-20px);
	}
	100% {
		transform: translatey(0px);
	}
}

.preload {
	background-color: $white;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;

	.image-arrangement {
		position: relative;
		display: flex;
		justify-content: center;
		width: 461px;
		margin-bottom: 16px;

		.people {
			animation: float 4s ease-in-out infinite;
		}

		.lamp {
			position: absolute;
			left: 108px;
			bottom: 24px;
			z-index: -1;
			animation: float 4.2s ease-in-out infinite;
		}
		.gear {
			position: absolute;
			bottom: 21px;
			z-index: -1;
			left: 90px;
			animation: float 4.15s ease-in-out infinite;
		}
		.paper-1 {
			position: absolute;
			top: 25px;
			z-index: -1;
			left: 112px;
			animation: float 4.1s ease-in-out infinite;
		}
		.paper-2 {
			position: absolute;
			bottom: 80px;
			z-index: 1;
			right: 117px;
			animation: float 4.25s ease-in-out infinite;
		}
	}

	&-text {
		display: flex;
		align-items: baseline;
		justify-content: center;
		margin-bottom: 4px;
		gap: 8px;
	}

	&-item {
		margin: 0 16px;
		img {
			max-width: 100%;
		}
		h1 {
			font-weight: 600;
			font-size: 22px;
			text-align: center;
			color: $dark-10;

			@include breakpoint(sm) {
				font-size: 18px;
			}
			@include breakpoint(xs) {
				font-size: 16px;
			}
		}

		p {
			font-weight: 400;
			font-size: 22px;
			text-align: center;
			color: $dark-10;

			@include breakpoint(sm) {
				font-size: 18px;
			}
			@include breakpoint(xs) {
				font-size: 16px;
			}
		}
	}
}
/* Team Profile */
.team-profile {
	position: relative;

	&-gap-left {
		padding: 25px 0 25px 0;

		@include breakpoint(md) {
			padding: 0;
		}
	}
	&-gap-right {
		padding: 25px 0 25px 0;

		@include breakpoint(md) {
			padding: 0;
		}
	}

	img.profile-illustrator {
		object-fit: contain;
		display: flex;
		margin-top: auto;
		padding: 0 30px;

		@include breakpoint(lg) {
			display: none;
		}
	}

	&::before {
		content: "";
		position: absolute;
		top: 140px;
		left: 0;
		right: 0;
		height: 1px;
		background-color: $gray-40;

		@include breakpoint(xxl) {
			top: 130px;
		}
		@include breakpoint(lg) {
			top: 98px;
		}

		@include breakpoint(md) {
			display: none;
		}
	}

	&::after {
		content: "";
		position: absolute;
		top: 838px;
		left: 0;
		right: 0;
		height: 1px;
		background-color: $gray-40;

		@include breakpoint(xxl) {
			top: 828px;
		}

		@include breakpoint(lg) {
			top: 796px;
		}

		@include breakpoint(md) {
			display: none;
		}
	}

	.divider > div {
		position: relative;
		&::after {
			content: "";
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			width: 1px;
			background-color: $gray-40;
		}
	}
}
.team-profile-progress {
	padding: 20px 0 45px;

	@include breakpoint(md) {
		padding: 0;
	}

	&-item {
		border: 1px solid $gray-40;
		border-radius: 10px;
		padding: 22px 24px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 40px;
		position: relative;

		@include breakpoint(3xl) {
			gap: 20px;
		}

		@include breakpoint(lg) {
			gap: 10px;
			padding: 16px;
			border-radius: 6px;
		}

		@include breakpoint(md) {
			background: $white;
			border-color: transparent;
			flex-wrap: wrap;
			row-gap: 30px;
			column-gap: 0;
		}

		&:not(:last-child) {
			margin-bottom: 16px;
		}

		&::before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			top: 68px;
			height: 1px;
			@include breakpoint(md) {
				background-color: $gray-40;
			}
		}

		.avatar-wrapper {
			flex: 0 0 auto;
			width: 270px;

			@include breakpoint(3xl) {
				width: 240px;
			}
			@include breakpoint(lg) {
				width: 150px;
			}
			@include breakpoint(md) {
				flex: 1 0 100%;
				order: 0;
			}
		}

		.progress-wrapper {
			flex: 1 1 auto;

			@include breakpoint(lg) {
				flex: 0 1 auto;
			}
			@include breakpoint(md) {
				order: 1;
			}
		}

		.date {
			font-size: 12px;
			min-width: 46px;

			@include breakpoint(md) {
				order: 2;
			}

			@include breakpoint(xssm) {
				font-size: 10px;
				min-width: 34px;
			}

			p {
				color: $gray-20;
				font-size: 12px;
				font-weight: 500;
				margin-bottom: 0;

				@include breakpoint(xssm) {
					font-size: 10px;
				}
			}
		}

		.badge {
			text-align: center;
			min-width: 92px;

			@include breakpoint(3xl) {
				min-width: 84px;
			}
			@include breakpoint(lg) {
				font-size: 10px;
				min-width: 70px;
			}
			@include breakpoint(md) {
				order: 4;
			}

			@include breakpoint(xssm) {
				font-size: 0;
				min-width: auto;
				padding: 0;
				width: 6px;
				height: 6px;
			}

			&.low {
				@include breakpoint(xssm) {
					background-color: $emerald;
				}
			}
			&.medium {
				@include breakpoint(xssm) {
					background-color: $blue-20;
				}
			}
			&.high {
				@include breakpoint(xssm) {
					background-color: $red;
				}
			}
		}

		.assignment {
			min-width: 220px;

			@include breakpoint(3xl) {
				min-width: 165px;
			}
			@include breakpoint(md) {
				order: 3;
			}
			@include breakpoint(xssm) {
				min-width: auto;
				flex: 0 0 auto;
			}
		}

		.dropdown {
			@include breakpoint(md) {
				position: absolute;
				top: 28px;
				right: 16px;
			}
		}
	}

	.progress {
		height: 8px;
		border-radius: 12px;
		background-color: $gray-900;

		@include breakpoint(lg) {
			display: none;
		}

		&-data {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: 8px;
			gap: 4px;

			@include breakpoint(lg) {
				margin-bottom: 0;
				flex-direction: column;
			}

			@include breakpoint(md) {
				align-items: flex-start;
				gap: 0;
			}

			p {
				font-weight: 500;
				font-size: 12px;
				color: $gray-20;
				margin-bottom: 0;

				@include breakpoint(3xl) {
					font-size: 12px;
				}
				@include breakpoint(xssm) {
					font-size: 10px;
				}
			}
			span {
				font-weight: 700;
				font-size: 12px;
				color: $dark-10;

				@include breakpoint(md) {
					font-weight: 500;
				}
				@include breakpoint(xssm) {
					font-size: 10px;
				}
			}
		}

		&-bar {
			background-color: $purple-10;
			border-radius: 12px;
		}
	}

	.avatar {
		display: flex;
		align-items: center;
		gap: 12px;

		@include breakpoint(lg) {
			gap: 8px;
		}

		&-letter {
			width: 40px;
			height: 40px;
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 100px;
			color: $white;
			font-weight: 700;
			font-size: 18px;
			flex: 0 0 auto;

			@include breakpoint(lg) {
				width: 32px;
				height: 32px;
				font-size: 16px;
			}
		}

		&-subtitle {
			font-weight: 500;
			font-size: 12px;
			color: $gray-20;
		}
		&-title {
			font-weight: 600;
			font-size: 14px;
			color: $dark-10;
			display: -webkit-box;
			-webkit-line-clamp: 1;
			-webkit-box-orient: vertical;
			overflow: hidden;
			@include breakpoint(xxl) {
				font-size: 12px;
			}
		}
	}

	.assignment {
		p {
			font-weight: 500;
			font-size: 12px;
			color: $gray-20;
			margin-bottom: 0;

			@include breakpoint(xxl) {
				font-size: 10px;
			}
		}

		background: $gray-100;
		border-radius: 100px;
		padding: 6px 16px;
		display: inline-flex;
		align-items: center;
		justify-content: space-between;
		gap: 20px;

		@include breakpoint(3xl) {
			gap: 12px;
		}

		@include breakpoint(xssm) {
			padding: 6px 12px;
		}
	}
}

/* Tabs */
.team-profile-progress .nav-pills .nav-link {
	padding: 7px 16px;
	border-radius: 4px;
	font-weight: 600;
	font-size: 12px;
	color: $purple-10;

	&.active {
		background: $purple-30;
		color: $purple-10;
	}
}
.team-profile-mobile {
	.nav-pills {
		background-color: $white;
		padding: 0 10px;
		gap: 12px;
		border-radius: 4px;
	}
	.nav-link {
		padding: 12px 6px;
		border-radius: 0;
		color: $gray-20;
		font-weight: 600;
		font-size: 12px;
		border-top-width: 3px;
		border-bottom-width: 3px;
		border-style: solid;
		border-color: transparent;

		&.active {
			background: transparent;
			color: $purple-10;
			border-bottom-color: $purple-10;
		}
	}
}

/* Main Profile */
.card-single-profile {
	border: 1px solid $gray-40;
	border-radius: 10px;
	position: relative;
	margin-top: 30px;
	background-color: $white;

	@include breakpoint(lg) {
		margin-top: 50px;
		margin-bottom: 30px;
		border-radius: 6px;
	}

	@include breakpoint(md) {
		margin-top: 40px;
	}

	@include breakpoint(md) {
		border-color: transparent;
	}

	&-wrapper {
		height: 100%;
		display: flex;
		flex-direction: column;

		@include breakpoint(lg) {
			height: auto;
		}
	}

	.dropdown.setting {
		position: absolute;
		top: 10px;
		right: 14px;
	}

	.setting {
		width: 36px;
		height: 36px;
		border-radius: 100px;
		background-color: $purple-30;
		display: flex;
		justify-content: center;
		align-items: center;

		@include breakpoint(3xl) {
			width: 28px;
			height: 28px;
		}

		iconify-icon {
			font-size: 20px;
			color: $purple-10;

			@include breakpoint(3xl) {
				font-size: 18px;
			}
		}
	}

	&-data {
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: center;
		gap: 16px;
		padding: 20px 30px;

		@include breakpoint(3xl) {
			padding: 20px 24px;
		}

		@include breakpoint(md) {
			gap: 20px;
		}

		.avatar-wrapper {
			margin-top: -60px;
			border-radius: 100px;

			@include breakpoint(md) {
				border: 8px solid $gray-10;
			}

			img {
				width: 72px;
				height: 72px;
				border-radius: 100px;
			}
		}
		h6 {
			font-weight: 700;
			font-size: 16px;
			color: $dark-10;
			margin-bottom: 2px;
		}
		p {
			font-weight: 600;
			font-size: 12px;
			color: $gray-20;
		}
	}

	&-statistic {
		border-top: 1px solid $gray-40;

		.statistic-divider {
			&:nth-child(2) {
				border-left: 1px solid $gray-40;
				border-right: 1px solid $gray-40;
			}
		}

		.data {
			padding: 16px 0;
			h6 {
				font-weight: 700;
				font-size: 16px;
				text-align: center;
				color: $dark-10;
				margin-bottom: 2px;

				@include breakpoint(xs) {
					font-size: 14px;
				}
			}
			p {
				font-weight: 500;
				font-size: 12px;
				text-align: center;
				color: $gray-20;

				@include breakpoint(xs) {
					font-size: 10px;
				}
			}
		}
	}
}

/* Login */
.card-login {
	padding: 50px 32px;
	background-color: $white;
	width: 370px;
	max-width: 100%;
	margin: 0 16px;

	@include breakpoint(xs) {
		padding: 40px 20px;
	}

	img {
		display: block;
		margin: auto;
		width: 136px;
	}

	p {
		font-weight: 400;
		font-size: 16px;
		text-align: center;
		color: $dark-10;
		margin: 20px 0 50px;
	}

	span {
		display: block;
		font-size: 12px;
		text-align: center;
		color: $gray-20;
		margin-top: 34px;
	}
}
