body {
	font-family: "Inter", sans-serif;
}
.overlay-dark {
	position: fixed;
	content: "";
	background-color: rgba($color: #000, $alpha: 0.3);
	inset: 0;
	z-index: 10;
}
.logo {
	margin-top: 30px;
	margin-bottom: 50px;
	width: 100%;

	@include breakpoint(md) {
		width: auto;
		margin: 0;
	}

	img.logo-inline {
		width: 136px;
		margin: 0 20px;
		@include breakpoint(xl) {
			display: none;
			margin: 0;
		}

		@include breakpoint(md) {
			display: block;
			width: 110px;
		}
	}
	img.logo-single {
		display: none;
		width: 40px;

		@include breakpoint(xl) {
			display: block;
			margin: auto;
		}
		@include breakpoint(md) {
			display: none;
		}
	}
}
img.logo-inline {
	width: 136px;
}
p,
h1,
h2 {
	margin-bottom: 0;
}
a {
	text-decoration: none;
	color: $gray-20;

	&:hover {
		color: $gray-20;
	}
}
body {
	background-color: $gray-10;
}
.h-screen {
	height: 100vh;
}
.default-custom-icon {
	color: $gray-20;
	size: 12px;
}
.bg-emerald {
	background-color: $emerald;
}
.bg-fuchia {
	background-color: $fuchia;
}
.bg-amber {
	background-color: $amber;
}
.bg-blue {
	background-color: $blue-20;
}
.bg-red {
	background-color: $red;
}
.bg-purple {
	background-color: $purple-10 !important;
}
.text-emerald {
	color: $emerald !important;
}
.text-fuchia {
	color: $fuchia !important;
}
.text-purple {
	color: $purple-10 !important;
}
.text-amber {
	color: $amber !important;
}
.text-blue {
	color: $blue-20 !important;
}
.text-red {
	color: $red !important;
}
.text-black {
	color: $dark !important;
}
.text-blue-sky {
	color: $blue-30 !important;
}
.content-item {
	overflow: hidden;
}
.content-item:not(:last-child) {
	margin-bottom: 52px;

	@include breakpoint(lg) {
		margin-bottom: 42px;
	}
	@include breakpoint(mdlg) {
		margin-bottom: 36px;
	}
	@include breakpoint(md) {
		margin-bottom: 28px;
	}
}
.link {
	color: $purple-10;
	font-size: 14px;

	&:hover {
		color: $purple-60;
	}
}
.badge-group {
	display: inline-flex;
	align-items: center;
	gap: 12px;
}
.badge {
	font-weight: 500;
	font-size: 12px;
	padding: 5px 10px;
	border-radius: 6px;
	flex: 0 0 auto;

	&-icon {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 6px;
		font-size: 14px;

		@include breakpoint(lg) {
			font-size: 12px;
		}

		p {
			font-weight: 500;
			color: $dark-10;
		}

		iconify-icon {
			font-size: 16px;
			color: $gray-20;
		}
	}

	&-default {
		background-color: $gray-10;

		@include breakpoint(md) {
			background-color: $gray-700;
		}
	}
	&-red {
		background-color: rgba($red, $alpha: 0.08);
		color: $red;
	}
	&-fuchia {
		background-color: rgba($fuchia, $alpha: 0.08);
		color: $fuchia;
	}
	&-emerald {
		background-color: rgba($emerald, $alpha: 0.08);
		color: $emerald;
	}
	&-amber {
		background-color: rgba($amber, $alpha: 0.08);
		color: $amber;
	}

	&-rounded {
		border-radius: 32px;
		padding: 8px 22px;
	}

	@include breakpoint(sm) {
		font-size: 10px;
	}
}
.dropdown-toggle.remove-arrow {
	&::after {
		display: none;
	}
}
iconify-icon {
	color: $gray-20;
	font-size: 16px;
}
// .weather-icon {
