// 	}
// }
.task-info-title {
	font-weight: 700;
	font-size: 28px;
	letter-spacing: -0.01em;
	color: $dark-10;
	margin: 30px 0 34px;

	@include breakpoint(lg) {
		font-size: 24px;
		margin: 20px 0;
	}
}
.task-info-detail {
	&-title {
		font-weight: 500;
		font-size: 14px;
		color: $gray-20;

		@include breakpoint(lg) {
			font-size: 12px;
		}
	}
	&-desc {
		font-weight: 500;
		font-size: 14px;
		color: $dark-10;

		@include breakpoint(lg) {
			font-size: 12px;
		}
	}
	&-checklist {
		margin-top: 10px;
	}
}
/* Loading */
.dot-wrapper {
	display: flex;
	align-items: center;
	gap: 5px;
}
.dots {
	width: 3px;
	height: 3px;
	border-radius: 50%;
}
.dot1 {
	animation: scaleInDown 1.6s -0.32s linear infinite;
	background: $dark;
}
.dot2 {
	animation: scaleInDown 1.6s -0.16s linear infinite;
	background: $dark;
}
.dot3 {
	animation: scaleInDown 1.6s linear infinite;
	background: $dark;
}

@keyframes scaleInDown {
	0%,
	80%,
	100% {
		-webkit-transform: scale(0);
		transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1);
		transform: scale(1);
	}
}
/* Page Loading */
.loading-page {
	background-color: $white;
	position: fixed;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	width: 100%;
	inset: 0;
	z-index: 99991;
	transition-duration: 500ms;
	font-size: 24px;
	font-weight: 500;

	img {
		width: 150px;
		margin-bottom: 14px;
	}

	.logo-wrapper {
		position: relative;
		margin-bottom: 10px;
		span {
			color: $purple-10;
			font-weight: 700;
			font-size: 14px;
			font-style: italic;
			position: absolute;
			right: 0;
			bottom: -8px;
		}
	}

	p {
		font-size: 16px;
	}

	span {
		color: $dark-10;
	}

	&-inner {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 10px;
	}

	&.done {
		opacity: 0;
		z-index: -1;
		transition-duration: 500ms;
	}
}
/* Input file button */
.inputfile-custom {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}
.text-sm {
	font-weight: 600;
	font-size: 14px;
	color: $dark-10;
}
.custom-scroll-gradient {
	// position: fixed;
	// background: linear-gradient(358.96deg, red 5.87%, rgba(255, 255, 255, 0) 99.17%);
	// height: 60px;
	// width: 100%;
	// bottom: 0;
	// left: 0;
	// right: 0;
	// z-index: 999999;
	// position: relative;
	position: relative;

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		right: 20px;
		// background: linear-gradient(358.96deg, #ffffff 5.87%, rgba(255, 255, 255, 0) 99.17%);
		background: linear-gradient(358.96deg, $white 5.87%, rgba(255, 255, 255, 0) 99.17%);
		height: 60px;
		// z-index: 999999;
	}
}
.custom-scroll {
	&::-webkit-scrollbar {
		width: 6px;
	}
	&::-webkit-scrollbar-thumb {
		background: $gray-800;
		border-radius: 4px;
	}
	&::-webkit-scrollbar-track {
		background: $gray-10;
		border-radius: 4px;
	}
}
.tooltip-inner {
	background: $dark-10;
	border-radius: 4px;
	color: rgba(198, 207, 219, 0.995);
	padding: 6px 12px;
	font-weight: 500;
	font-size: 10px;
}
.bs-tooltip-auto[data-popper-placement^="top"] .tooltip-arrow::before {
	border-top-color: $dark-10;
}
.bs-tooltip-auto[data-popper-placement^="right"] .tooltip-arrow::before {
	border-right-color: $dark-10;
}
.bs-tooltip-auto[data-popper-placement^="bottom"] .tooltip-arrow::before {
	border-bottom-color: $dark-10;
}
.bs-tooltip-auto[data-popper-placement^="left"] .tooltip-arrow::before {
	border-left-color: $dark-10;
}
.dropdown,
.form-check-input {
	&:focus {
		box-shadow: none;
		border-color: $gray-1500;
	}
}
.form-check-label,
.form-check-input {
	user-select: none;
	cursor: pointer;
}
.form-check-label {
	color: $dark-10;
	font-weight: 500;
	font-size: 14px;
	margin-left: 4px;

	@include breakpoint(lg) {
		font-size: 12px;
	}
}
.form-check-input {
	background-color: $gray-1500;
	border-color: $gray-1500;
	width: 18px;
	height: 18px;

	&:checked {
		background-color: $purple-10;
		border-color: $purple-10;

		+ {
			.form-check-label {
				text-decoration: line-through;
			}
		}
	}
}
.form-check-input[type="checkbox"] {
	border-radius: 6px;
}
.form-check-input:checked[type="checkbox"] {
	background-size: 14px;
}
.form-check:not(:last-child) {
	margin-bottom: 8px;
}
iconify-icon.form-icon {
	font-size: 24px;
	color: $gray-20;
	position: absolute;
	top: 0;
	bottom: 0;
	right: 20px;
	display: flex;
	align-items: center;
	cursor: pointer;

	@include breakpoint(md) {
		font-size: 20px;
	}
	@include breakpoint(sm) {
		font-size: 18px;
		right: 16px;
	}
}
.pointer-event-none {
	pointer-events: none;
}
.dropdown-toggle {
	iconify-icon {
		transition: 0.3s all ease;
	}
	&.animate {
		&.show {
			iconify-icon {
				transform: rotate(135deg);
			}
		}
	}
}
.member-assignment {
	font-weight: 500;
	font-size: 14px;
	color: $gray-20;

	@include breakpoint(sm) {
		font-size: 12px;
	}
}
.show-more {
	font-weight: 500;
	font-size: 12px;
	letter-spacing: -0.02em;
	color: $dark-10;
	text-align: center;
	display: block;
}
