  line-height: 6px;
  font-size: 4px;
}

.dataTable.no-footer {
  border-bottom: 0px;
  border-color: #f1f4fa;
}

table.dataTable thead > tr > th.sorting_asc:before,
table.dataTable thead > tr > th.sorting_desc:after {
  opacity: 1;
}

/* Breadcrumb */
.page-link,
.page-item:first-child .page-link,
.page-item:last-child .page-link {
  border: 0;
  padding: 0;
  margin: 0;
  border-radius: 100px;
}
.page-link:focus,
.page-item:first-child .page-link:focus,
.page-item:last-child .page-link:focus {
  box-shadow: none;
}

.page-item {
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-item a {
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pagination {
  gap: 10px;
  align-items: center;
}

.page-link {
  color: #809fb8;
}

.active > .page-link {
  background-color: #f4f7fa;
  border-color: #f4f7fa;
  color: #000;
}

/* Datatables */
div.dataTables_wrapper div.dataTables_info {
  padding-top: 0;
}

table.dataTable.hover > tbody > tr,
table.dataTable.display > tbody > tr {
  transition: 0.4s all ease;
}

table.dataTable.hover > tbody > tr:hover > *,
table.dataTable.display > tbody > tr:hover > * {
  background-color: #fafbff;
}

/* Quill JS */
.ql-toolbar,
.ql-container,
.ql-editor {
  font-family: "Inter", sans-serif;
}

.ql-toolbar.ql-snow {
  border-color: #dde6ee;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}
@media (max-width: 767.98px) {
  .ql-toolbar.ql-snow {
    background-color: #fff;
  }
}

.ql-container.ql-snow {
  border-color: #dde6ee;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  height: 165px;
}
@media (max-width: 767.98px) {
  .ql-container.ql-snow {
    background-color: #fff;
  }
}

.ql-editor {
  padding: 14px 15px;
  font-size: 14px;
}
@media (max-width: 575.98px) {
  .ql-editor {
    padding: 12px 16px;
    font-size: 12px;
  }
}

.ql-editor.ql-blank::before {
  font-weight: 500;
  font-style: normal;
  font-size: 14px;
  color: #809fb8;
}
@media (max-width: 767.98px) {
  .ql-editor.ql-blank::before {
    font-size: 12px;
  }
}

/* Filepond */
.filepond--root {
  min-height: 112px;
}

.filepond--root .filepond--drop-label {
  min-height: 112px;
}

.filepond--drip {
  background-color: #fff;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='6' ry='6' stroke='%23C2D5E4FF' stroke-width='2' stroke-dasharray='12%2c 12' stroke-dashoffset='5' stroke-linecap='round'/%3e%3c/svg%3e");
  border-radius: 6px;
  opacity: 1;
}

.filepond--drop-label {
  font-size: 14px;
  color: #809fb8;
}
@media (max-width: 767.98px) {
  .filepond--drop-label {
    background-image: none;
    border: 1px solid #dde6ee;
  }
}
.filepond--drop-label span {
  color: #364353;
  text-decoration: none;
}

@media (max-width: 575.98px) {
  .filepond--root *:not(text) {
    font-size: 12px;
  }
}

@media (max-width: 399.98px) {
  .ql-toolbar.ql-snow .ql-formats {
    margin-right: 2px;
