  font-size: 16px;
}

.task-info-title {
  font-weight: 700;
  font-size: 28px;
  letter-spacing: -0.01em;
  color: #364353;
  margin: 30px 0 34px;
}
@media (max-width: 991.98px) {
  .task-info-title {
    font-size: 24px;
    margin: 20px 0;
  }
}

.task-info-detail-title {
  font-weight: 500;
  font-size: 14px;
  color: #809fb8;
}
@media (max-width: 991.98px) {
  .task-info-detail-title {
    font-size: 12px;
  }
}
.task-info-detail-desc {
  font-weight: 500;
  font-size: 14px;
