/* Loading */
.dot-wrapper {
  display: flex;
  align-items: center;
  gap: 5px;
}

.dots {
  width: 3px;
  height: 3px;
  border-radius: 50%;
}

.dot1 {
  animation: scaleInDown 1.6s -0.32s linear infinite;
  background: #000;
}

.dot2 {
  animation: scaleInDown 1.6s -0.16s linear infinite;
  background: #000;
}

.dot3 {
  animation: scaleInDown 1.6s linear infinite;
  background: #000;
}

@keyframes scaleInDown {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
/* Page Loading */
.loading-page {
  background-color: #fff;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  inset: 0;
  z-index: 99991;
  transition-duration: 500ms;
  font-size: 24px;
  font-weight: 500;
}
.loading-page img {
  width: 150px;
  margin-bottom: 14px;
}
.loading-page .logo-wrapper {
  position: relative;
  margin-bottom: 10px;
}
.loading-page .logo-wrapper span {
  color: #6c5ce7;
  font-weight: 700;
  font-size: 14px;
  font-style: italic;
  position: absolute;
  right: 0;
  bottom: -8px;
}
.loading-page p {
  font-size: 16px;
}
.loading-page span {
  color: #364353;
}
.loading-page-inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.loading-page.done {
  opacity: 0;
  z-index: -1;
  transition-duration: 500ms;
}

/* Input file button */
.inputfile-custom {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.text-sm {
  font-weight: 600;
  font-size: 14px;
  color: #364353;
}

.custom-scroll-gradient {
  position: relative;
}
.custom-scroll-gradient::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 20px;
  background: linear-gradient(358.96deg, #fff 5.87%, rgba(255, 255, 255, 0) 99.17%);
  height: 60px;
}

.custom-scroll::-webkit-scrollbar {
  width: 6px;
}
.custom-scroll::-webkit-scrollbar-thumb {
  background: #d3dfe9;
  border-radius: 4px;
}
.custom-scroll::-webkit-scrollbar-track {
  background: #f1f4fa;
  border-radius: 4px;
}

.tooltip-inner {
  background: #364353;
  border-radius: 4px;
  color: rgba(198, 207, 219, 0.995);
  padding: 6px 12px;
  font-weight: 500;
  font-size: 10px;
}

.bs-tooltip-auto[data-popper-placement^=top] .tooltip-arrow::before {
  border-top-color: #364353;
}

.bs-tooltip-auto[data-popper-placement^=right] .tooltip-arrow::before {
  border-right-color: #364353;
}

.bs-tooltip-auto[data-popper-placement^=bottom] .tooltip-arrow::before {
  border-bottom-color: #364353;
}

.bs-tooltip-auto[data-popper-placement^=left] .tooltip-arrow::before {
  border-left-color: #364353;
}

.dropdown:focus,
.form-check-input:focus {
  box-shadow: none;
  border-color: #dbdbdb;
}

.form-check-label,
.form-check-input {
  user-select: none;
  cursor: pointer;
}

.form-check-label {
  color: #364353;
  font-weight: 500;
  font-size: 14px;
  margin-left: 4px;
}
@media (max-width: 991.98px) {
  .form-check-label {
    font-size: 12px;
