  }
}

.pointer-event-none {
  pointer-events: none;
}

.dropdown-toggle iconify-icon {
  transition: 0.3s all ease;
}
.dropdown-toggle.animate.show iconify-icon {
  transform: rotate(135deg);
}

.member-assignment {
  font-weight: 500;
  font-size: 14px;
  color: #809fb8;
}
@media (max-width: 575.98px) {
