.hamburger-container {
	width: 22px;
	height: 18px;
	position: relative;
	margin: 0 auto;
	cursor: pointer;
	transition: 0.2s;

	span {
		display: block;
		position: absolute;
		height: 2px;
		transform: rotate(0deg);
		transition: all 0.25s cubic-bezier(0.22, 0.61, 0.36, 1);
		opacity: 1;
		width: 100%;
		border-radius: 50px;
		left: 0;
	}
}

.hamburger {
	&-purple {
		background-color: $purple-10;
	}

	span {
		&:nth-child(1) {
			top: 0px;
		}

		&:nth-child(2),
		&:nth-child(3) {
			top: 8px;
		}

		&:nth-child(4) {
			top: 16px;
		}
	}
}

label[aria-expanded="true"] {
	> .hamburger-container {
		> span {
			&:nth-child(1) {
				top: 18px;
				width: 0%;
				left: 50%;
			}

			&:nth-child(2) {
				transform: rotate(45deg);
			}

			&:nth-child(3) {
				transform: rotate(-45deg);
			}

			&:nth-child(4) {
				top: 18px;
				width: 0%;
				left: 50%;
			}
		}
	}
}
