table.dataTable thead > tr > th.sorting {
	&:before,
	&:after {
		line-height: 6px;
		font-size: 4px;
	}
}
.dataTable.no-footer {
	border-bottom: 0px;
	border-color: $gray-10;
}
table.dataTable thead > tr > th.sorting_asc:before,
table.dataTable thead > tr > th.sorting_desc:after {
	opacity: 1;
}

/* Breadcrumb */
.page-link,
.page-item:first-child .page-link,
.page-item:last-child .page-link {
	border: 0;
	padding: 0;
	margin: 0;
	border-radius: 100px;

	&:focus {
		box-shadow: none;
	}
}
.page-item {
	width: 36px;
	height: 36px;
	display: flex;
	justify-content: center;
	align-items: center;

	a {
		width: 36px;
		height: 36px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
}

.pagination {
	gap: 10px;
	align-items: center;
}
.page-link {
	color: $gray-20;
}
.active > .page-link {
	background-color: $gray-100;
	border-color: $gray-100;
	color: $dark;
}
/* Datatables */
div.dataTables_wrapper div.dataTables_info {
	padding-top: 0;
}
table.dataTable.hover > tbody > tr,
table.dataTable.display > tbody > tr {
	transition: 0.4s all ease;
}
table.dataTable.hover > tbody > tr:hover > *,
table.dataTable.display > tbody > tr:hover > * {
	background-color: $purple-50;
}
/* Quill JS */
.ql-toolbar,
.ql-container,
.ql-editor {
	font-family: "Inter", sans-serif;
}
.ql-toolbar.ql-snow {
	border-color: $gray-500;
	border-top-left-radius: 6px;
	border-top-right-radius: 6px;

	@include breakpoint(md) {
		background-color: $white;
	}
}
.ql-container.ql-snow {
	border-color: $gray-500;
	border-bottom-left-radius: 6px;
	border-bottom-right-radius: 6px;
	height: 165px;

	@include breakpoint(md) {
		background-color: $white;
	}
}
.ql-editor {
	padding: 14px 15px;
	font-size: 14px;

	@include breakpoint(sm) {
		padding: 12px 16px;
		font-size: 12px;
	}
}
.ql-editor.ql-blank::before {
	font-weight: 500;
	font-style: normal;
	font-size: 14px;
	color: $gray-20;

	@include breakpoint(md) {
		font-size: 12px;
	}
}
/* Filepond */
.filepond--root {
	min-height: 112px;
}
.filepond--root .filepond--drop-label {
	min-height: 112px;
}
.filepond--drip {
	background-color: $white;
	background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='6' ry='6' stroke='%23C2D5E4FF' stroke-width='2' stroke-dasharray='12%2c 12' stroke-dashoffset='5' stroke-linecap='round'/%3e%3c/svg%3e");
	border-radius: 6px;
	opacity: 1;
}
.filepond--drop-label {
	font-size: 14px;
	color: $gray-20;

	@include breakpoint(md) {
		background-image: none;
		border: 1px solid $gray-500;
	}

	span {
		color: $dark-10;
		text-decoration: none;
	}
}

.filepond--root *:not(text) {
	@include breakpoint(sm) {
		font-size: 12px;
	}
}

.ql-toolbar.ql-snow .ql-formats {
	@include breakpoint(xs) {
		margin-right: 2px;
	}
}
.ql-snow .ql-picker.ql-header {
	@include breakpoint(xs) {
		width: 80px;
	}
}
