/* --------------------------------------------------------------------------
 [TABLE OF CONTENT]

 1. Navbar
-------------------------------------------------------------------------- */

@import "variables";
@import "breakpoints";
@import "mixin";

/* --------------------------------------------------------------------------
1. General
 -------------------------------------------------------------------------- */
.title-action {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 20px;

	@include breakpoint(lg) {
		margin-bottom: 14px;
	}

	@include breakpoint(md) {
		margin: 0 20px 20px;
	}

	@include breakpoint(sm) {
		margin: 0 16px 18px;
	}

	+ {
		.row {
			@include breakpoint(md) {
				padding: 0 20px 0;
			}

			@include breakpoint(sm) {
				padding: 0 16px 0;
			}
		}

		.carousel {
			@include breakpoint(md) {
				padding: 0 0 0 20px;
			}

			@include breakpoint(sm) {
				padding: 0 0 0 16px;
			}
		}
	}

	a.link {
		@include breakpoint(sm) {
			display: none;
		}
	}
	a.arrow-link {
		display: none;
		@include breakpoint(sm) {
			display: flex;
		}
	}

	&.date-range {
		@include breakpoint(sm) {
			// flex-direction: column;
			// align-items: flex-start;
			gap: 8px;
		}

		// .form-daterange-select {
		// 	@include breakpoint(sm) {
		// 		width: 100%;
		// 	}
		// }
	}

	span.more {
		display: none;

		@include breakpoint(sm) {
			display: inline-flex;
		}

		iconify-icon {
			color: $purple-10;
			font-size: 18px;
		}
	}

	h2 {
		font-weight: 700;
		font-size: 16px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $dark-10;

		@include breakpoint(lg) {
			font-size: 14px;
		}
		@include breakpoint(md) {
			font-size: 12px;
		}

		span {
			@include breakpoint(400px) {
				display: none;
			}
		}
	}
}
.gap-content {
	@include breakpoint(md) {
		margin: 0 20px 20px;
	}

	@include breakpoint(sm) {
		margin: 0 16px 18px;
	}
}
.title-content {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 26px;

	@include breakpoint(lg) {
		margin-bottom: 20px;
	}

	@include breakpoint(md) {
		margin: 0 20px 20px;
	}

	@include breakpoint(sm) {
		margin: 0 16px 18px;
		// flex-direction: column;
		// align-items: flex-start;
		gap: 10px;
	}

	&.team {
		margin-bottom: 18px;
	}

	h2 {
		font-weight: 500;
		font-size: 16px;
		color: $dark-10;

		&.small {
			@include breakpoint(lg) {
				font-size: 14px;
			}
			@include breakpoint(xs) {
				font-size: 12px;
			}
		}
	}

	&-action {
		display: inline-flex;
		gap: 12px;
		align-items: center;

		&.wide {
			@include breakpoint(sm) {
				width: 100%;
				justify-content: space-between;
			}
		}

		.sort-data {
			display: inline-flex;
			align-items: center;
			border: 1px solid $gray-40;
			border-radius: 4px;
			padding: 6px 12px;
			background-color: $white;

			&.no-border {
				border-color: transparent;
				padding: 0;
			}

			span {
				flex: 1 0 auto;
				color: $gray-20;
				font-size: 12px;
				font-weight: 500;

				@include breakpoint(xs) {
					font-size: 10px;
				}
			}
		}
		.form-select {
			cursor: pointer;
			padding: 0 16px 0 4px;
			font-size: 12px;
			font-weight: 500;
			border-color: transparent;
			background: url(https://api.iconify.design/bi/caret-down-fill.svg?color=%23809fb8&width=10&height=10) no-repeat right 0 center;

			&.arrow-dark {
				background: url("https://api.iconify.design/bi/caret-down-fill.svg?color=%23364353&width=10&height=10") no-repeat right 0 center;
			}

			@include breakpoint(xs) {
				font-size: 10px;
			}

			&:active,
			&:focus {
				box-shadow: none;
				border-color: transparent;
			}
		}
	}
}
/* --------------------------------------------------------------------------
2. Main Layout
 -------------------------------------------------------------------------- */
.main-content {
	@include breakpoint(md) {
		margin-bottom: 60px;
	}

	@include breakpoint(sm) {
		margin-bottom: 30px;
	}

	&.normal-content,
	.normal-content,
	.alert {
		overflow: hidden;
		@include breakpoint(md) {
			margin: 0 20px 20px;
		}

		@include breakpoint(sm) {
			margin: 0 16px 18px;
		}
	}
}
.dashboard-layout {
	display: initial;
	transition-duration: 0.3s;

	aside {
		&::-webkit-scrollbar {
			display: none;
		}

		padding: 38px 35px;
		display: flex;
		align-items: flex-start;
		flex-direction: column;
		width: 328px;
		overflow-y: auto;
		overflow-x: hidden;
		position: fixed;
		transition-duration: 300ms;
		z-index: 12;
		top: 0;
		bottom: 0;

		&.show {
			left: 0;
		}

		.logo {
			@include breakpoint(md) {
				margin-bottom: 30px;
				margin-left: 28px;
				margin-top: 56px;
			}
			@include breakpoint(md) {
				margin-top: 36px;
			}
		}

		.avatar {
			margin: 14px 28px 34px;
		}

		.need-support {
			width: 100%;
			padding: 0 28px;
			a {
				width: 100%;
			}
		}

		@include breakpoint(xxl) {
			padding: 30px 26px;
			width: 280px;
		}
		@include breakpoint(xl) {
			padding: 20px 16px;
			width: 100px;
		}
		@include breakpoint(md) {
			width: 252px;
			left: -252px;
			background-color: $white;
			padding: 0;
		}
	}

	main {
		background-color: $white;
		margin: 38px 40px;
		transition-duration: 0.3s;
		margin-left: 328px;
		border-radius: 16px;
		padding: 50px 40px;
		min-height: calc(100vh - 76px);

		@include breakpoint(xxl) {
			padding: 40px 30px;
			margin-left: 280px;
		}
		@include breakpoint(xl) {
			margin: 34px 30px;
			margin-left: 100px;
		}
		@include breakpoint(lg) {
			padding: 24px 30px;
		}
		@include breakpoint(md) {
			background-color: $gray-10;
			margin-left: 0;
			margin: 0;
			padding: 0;
		}
	}
}
/* --------------------------------------------------------------------------
3. Sidebar
 -------------------------------------------------------------------------- */
aside {
	.title-menu {
		font-weight: 700;
		margin-top: 50px;
		margin-bottom: 20px;
		font-size: 13px;
		color: $dark-10;
		text-transform: uppercase;
		letter-spacing: 0.17em;
		padding: 14px 20px;

		@include breakpoint(xl) {
			display: none;
		}
	}
}

.menu-list {
	padding: 0;
	margin: 0;
	list-style-type: none;
	width: 100%;

	&-item {
		@include breakpoint(xl) {
			text-align: center;
		}
		@include breakpoint(md) {
			text-align: left;
			margin: 0 28px;
		}

		&:not(:last-child) {
			margin-bottom: 10px;

			@include breakpoint(xl) {
				margin-bottom: 20px;
			}

			@include breakpoint(md) {
				margin-bottom: 10px;
			}

			@include breakpoint(sm) {
				margin-bottom: 0;
			}
		}

		&.active {
			a {
				background-color: $purple-20;

				@include breakpoint(md) {
					background-color: transparent;
				}

				iconify-icon,
				p {
					color: $purple-10;
				}
			}
		}

		a {
			&:hover {
				background-color: $purple-20;

				@include breakpoint(md) {
					background-color: transparent;
				}

				iconify-icon,
				p {
					color: $purple-10;
				}
			}

			display: flex;
			align-items: center;
			gap: 16px;
			transition: 0.4s all ease;
			border-radius: 8px;
			padding: 14px 20px;

			@include breakpoint(xl) {
				display: inline-flex;
				padding: 0;
				width: 45px;
				height: 45px;
				justify-content: center;
				border-radius: 100px;
			}

			@include breakpoint(md) {
				justify-content: flex-start;
				width: auto;
				height: auto;
				display: flex;
				border-radius: 8px;
				padding: 14px 0;
				gap: 10px;
			}
		}
		iconify-icon {
			transition: 0.4s all ease;
			font-size: 20px;
		}
		p {
			transition: 0.4s all ease;
			font-weight: 600;

			@include breakpoint(xl) {
				display: none;
			}

			@include breakpoint(md) {
				display: block;
				font-weight: 500;
				color: $dark;
				font-size: 14px;
			}

			@include breakpoint(sm) {
				font-size: 12px;
			}
		}
	}
}
/* --------------------------------------------------------------------------
3. Help
 -------------------------------------------------------------------------- */
.help {
	position: relative;
	margin-top: auto;

	@include breakpoint(md) {
		display: none;
	}

	&-inner {
		background-color: $white;
		padding: 34px 28px;
		border-radius: 16px;

		@include breakpoint(xl) {
			display: none;
		}
	}

	.icon-bagde {
		position: absolute;
		top: -30px;
		left: 0;
		right: 0;
		margin: auto;
		background-color: $purple-10;
		width: 62px;
		height: 62px;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 100px;
		border: 8px solid $gray-10;
		transition: 0.4s all ease;

		@include breakpoint(xl) {
			position: relative;
			top: 0;
		}

		iconify-icon {
			font-size: 24px;
			color: $white;
		}
	}

	h6 {
		font-weight: 700;
		text-align: center;
		color: $dark-10;
		margin-top: 16px;
	}
	p {
		font-weight: 500;
		font-size: 16px;
		text-align: center;
		color: $gray-20;
		margin-bottom: 26px;
	}
}
.gap-help {
	margin-bottom: 50px;

	@include breakpoint(xl) {
		position: relative;
		margin-bottom: 0;
		margin-top: 40px;
	}

	@include breakpoint(md) {
		margin-bottom: 40px;
	}

	&::before {
		@include breakpoint(xl) {
			position: absolute;
			content: "";
			background-color: $gray-300;
			width: 24px;
			height: 1px;
			top: -20px;
			left: 32%;
		}
		@include breakpoint(md) {
			background-color: $gray-1300;
			width: auto;
			left: 0;
			right: 0;
		}
	}

	&::after {
		@include breakpoint(md) {
			position: absolute;
			content: "";
			background-color: $gray-1300;
			height: 1px;
			bottom: -20px;
			width: auto;
			left: 0;
			right: 0;
		}
	}
}
/* --------------------------------------------------------------------------
3. Buttons
 -------------------------------------------------------------------------- */
.btn {
	flex: 0 0 auto;
	transition: 0.4s all ease;
	border-radius: 8px;
	border: transparent;
	padding: 14px;
	font-weight: 600;
	display: inline-flex;
	justify-content: center;
	align-items: center;

	&:focus,
	&:active,
	&:active:focus {
		box-shadow: none;
	}

	iconify-icon {
		margin-right: 6px;
	}

	@include breakpoint(md) {
		font-size: 12px;
	}

	&-purple {
		&-solid {
			background-color: $purple-10;
			color: $white;

			&:hover,
			&:focus {
				background-color: $purple-60;
				color: $white;
			}

			iconify-icon {
				color: $white;
			}
		}

		background-color: $purple-30;
		color: $purple-10;

		&:hover,
		&:focus {
			background-color: $purple-40;
			color: $purple-10;
		}
	}

	&-link {
		text-decoration: none;
		padding: 0;
		font-size: 14px;
		font-weight: 500;

		iconify-icon {
			color: $purple-10;
		}
	}

	&-secondary {
		background-color: $gray-60;
		color: $gray-70;

		&:hover,
		&:focus {
			background-color: $gray-80;
		}
	}

	&-transparent {
		color: $purple-10;

		&:hover,
		&:focus {
			color: $purple-10;
		}
	}

	&-md {
		padding: 14px 30px;
		font-size: 16px;
		font-weight: 600;
		border-radius: 6px;

		@include breakpoint(sm) {
			font-size: 14px;
			padding: 12px 26px;
			justify-content: center;
			flex: 1 0 auto;
		}

		@include breakpoint(xs) {
			font-size: 12px;
			padding: 12px 18px;
		}

		iconify-icon {
			@include breakpoint(xs) {
				font-size: 14px;
			}
		}
	}

	&-sm {
		padding: 8px 12px;
		font-size: 12px;
		font-weight: 500;
		border-radius: 4px;

		@include breakpoint(xs) {
			font-size: 10px;
		}

		iconify-icon {
			@include breakpoint(xs) {
				font-size: 14px;
			}
		}
	}
}

/* --------------------------------------------------------------------------
1. Navbar
 -------------------------------------------------------------------------- */
nav.navbar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 40px;
	flex-wrap: wrap;

	@include breakpoint(lg) {
		margin-bottom: 24px;
	}

	@include breakpoint(md) {
		padding: 50px 20px 0;
	}

	@include breakpoint(sm) {
		padding: 30px 16px 0;
		margin-bottom: 20px;
	}

	.title-weather {
		@include breakpoint(md) {
			order: 3;
			flex: 1 0 100%;
			margin-top: 30px;
		}
	}

	img.img-weather {
		width: 36px;
		height: 36px;
		object-fit: contain;
		display: none;

		@include breakpoint(xl) {
			display: flex;
		}
		@include breakpoint(xs) {
			width: 32px;
			height: 32px;
		}
	}

	.title {
		font-weight: 600;
		font-size: 26px;
		color: $dark-10;

		@include breakpoint(lg) {
			font-size: 22px;
		}
		@include breakpoint(mdlg) {
			font-size: 18px;
		}
	}
	.navbar-item {
		width: 50px;
		height: 50px;
		border: 2px solid $gray-30;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 100px;

		&:hover {
			cursor: pointer;
		}

		&:last-child {
			@include breakpoint(md) {
				display: none;
			}
		}

		@include breakpoint(sm) {
			width: 40px;
			height: 40px;
		}
		// @include breakpoint(mdlg) {
		// 	width: 32px;
		// 	height: 32px;
		// }

		#hamburger-menu {
			display: none;

			@include breakpoint(md) {
				display: block;
			}
			@include breakpoint(sm) {
				transform: scale(0.7);
			}
		}

		iconify-icon {
			color: $gray-20;
		}

		&.search {
			background-color: $purple-50;
			iconify-icon {
				font-size: 20px;
				@include breakpoint(sm) {
					font-size: 16px;
				}
			}
		}

		&.question-marks {
			@include breakpoint(md) {
				order: 3;
				background-color: $purple-50;
			}
			iconify-icon {
				font-size: 30px;

				@include breakpoint(md) {
					display: none;
				}

				@include breakpoint(sm) {
					font-size: 26px;
				}
			}
		}

		&.bell {
			@include breakpoint(md) {
				background-color: $purple-50;
			}
			iconify-icon {
				font-size: 19px;
				@include breakpoint(sm) {
					font-size: 16px;
				}
			}
			.dot-notification {
				position: relative;
				&::after {
					content: "";
					background-color: $red;
					position: absolute;
					top: 2px;
					right: 0;
					width: 5px;
					height: 5px;
					border-radius: 100px;
				}
			}
		}

		&.avatar {
			border: 0;
			overflow: hidden;

			img {
				width: 100%;
			}
		}
	}
	.navbar-item-wrapper {
		display: flex;
		align-items: center;
		gap: 16px;

		@include breakpoint(sm) {
			gap: 10px;
		}

		@include breakpoint(xssm) {
			gap: 6px;
		}

		@include breakpoint(xs) {
			gap: 4px;
		}
	}
}
/* Form */
.form-daterange-select {
	border: 1px solid $gray-40;
	border-radius: 4px;
	padding: 8px 15px;
	display: inline-flex;
	align-items: center;
	justify-content: space-between;
	gap: 10px;
	font-weight: 500;
	font-size: 12px;
	line-height: 15px;
	background-color: $white;

	&:hover {
		cursor: pointer;
	}

	iconify-icon {
		font-size: 14px;
		@include breakpoint(sm) {
			font-size: 12px;
		}
	}
}
/* Card */
.card-statistic {
	border: 1px solid $gray-40;
	border-radius: 12px;
	padding: 21px 26px;
	display: flex;
	align-items: center;
	gap: 16px;

	@include breakpoint(lg) {
		padding: 14px 16px;
		border-radius: 6px;
		gap: 12px;
	}

	@include breakpoint(mdlg) {
		padding: 12px 14px;
	}

	@include breakpoint(md) {
		background-color: $white;
		border-color: transparent;
	}

	@include breakpoint(xs) {
		padding: 8px 10px;
	}

	&-content {
		flex: 1 0 auto;

		label {
			font-weight: 500;
			font-size: 12px;
			color: $gray-20;
		}

		span.total {
			font-weight: 700;
			font-size: 14px;

			@include breakpoint(lg) {
				font-weight: 500;
				font-size: 12px;
			}
		}

		p {
			font-weight: 700;
			font-size: 16px;
			color: $dark-10;
			margin-top: 2px;

			@include breakpoint(lg) {
				font-size: 12px;
				margin-top: 0;
			}

			@include breakpoint(mdlg) {
				font-size: 12px;
			}

			span {
				font-weight: 500;
			}
		}
	}

	&-icon {
		width: 48px;
		height: 48px;
		border-radius: 8px;
		display: flex;
		justify-content: center;
		align-items: center;

		@include breakpoint(lg) {
			width: 38px;
			height: 38px;
			border-radius: 6px;
		}

		@include breakpoint(mdlg) {
			width: 32px;
			height: 32px;
		}

		iconify-icon {
			font-size: 30px;
			color: $white;

			@include breakpoint(lg) {
				font-size: 24px;
			}
		}
	}
}
.card-user {
	border: 1px solid $gray-40;
	border-radius: 12px;
	padding: 32px 0 35px;
	position: relative;

	@include breakpoint(lg) {
		padding: 14px;
		display: flex;
		gap: 8px;
		border-radius: 6px;
	}

	@include breakpoint(mdlg) {
		flex-direction: column;
	}

	@include breakpoint(md) {
		padding: 18px 14px;
		background-color: $white;
		border-color: transparent;
	}

	&.bordered {
		border: 1px solid $gray-40;
	}

	&-avatar {
		text-align: center;

		.avatar-wrapper {
			position: relative;
			display: inline-block;

			img {
				overflow: hidden;
				border-radius: 100px;

				@include breakpoint(lg) {
					width: 42px;
					height: 42px;
				}
			}

			span {
				position: absolute;
				width: 24px;
				height: 24px;
				background-color: $white;
				border-radius: 100px;
				display: flex;
				justify-content: center;
				align-items: center;
				top: -2px;
				right: -4px;

				@include breakpoint(lg) {
					width: 18px;
					height: 18px;
				}

				iconify-icon {
					font-size: 18px;

					@include breakpoint(lg) {
						font-size: 14px;
					}
				}
			}
		}
	}
	&-content {
		text-align: center;

		@include breakpoint(lg) {
			text-align: left;
		}

		@include breakpoint(mdlg) {
			text-align: center;
		}
