	}
	.upload-file-icon {
		cursor: pointer;
		position: absolute;
		bottom: -6px;
		right: 6px;
		background: $purple-10;
		border: 3px solid $white;
		width: 36px;
		height: 36px;
		border-radius: 100px;
		display: flex;
		justify-content: center;
		align-items: center;

		iconify-icon {
			color: $white;
			font-size: 18px;
		}
	}
	.file-infomation {
		font-weight: 500;
		font-size: 14px;
		text-align: center;
		color: $gray-20;
		margin-bottom: 40px;

		span {
			color: $dark-10;
		}
	}
	&-add {
		.modal-content {
			padding: 50px 0 56px;
		}
	}
	&-delete {
		img {
			width: 170px;
			margin: auto;
			padding: 34px 0 26px;
		}

		p {
			font-weight: 500;
			font-size: 20px;
			text-align: center;
			letter-spacing: -0.01em;
			color: $dark-10;

			strong {
				font-weight: 600;
			}
		}
	}
	.close-modal {
		cursor: pointer;
		position: absolute;
		top: 18px;
		right: 18px;
		width: 32px;
		height: 32px;
		background-color: $gray-100;
		border-radius: 100px;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: 0.4s all ease;

		@include breakpoint(xs) {
			top: 16px;
			right: 16px;
		}

		&:hover {
			background-color: $gray-1200;
		}

		iconify-icon {
			font-size: 20px;
		}
	}
	.modal-btn-group {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 14px;
		margin: 28px;

		button {
			flex: 1 0 auto;
			justify-content: center;
			font-size: 14px;
		}
	}
}

.attachment {
	&-area {
		background-color: $gray-10;
		border-radius: 10px;
		padding: 20px;

		@include breakpoint(md) {
			background-color: $gray-700;
			margin-top: 36px;
		}

		h6 {
			font-weight: 500;
			font-size: 14px;
			color: $gray-20;
			margin-bottom: 12px;

			@include breakpoint(md) {
				font-size: 12px;
			}
		}
	}
	&-wrapper {
		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}
	&-group {
		background: $white;
		border-radius: 6px;
		padding: 20px;
	}
	&-asset {
		flex: 0 0 auto;
		width: 40px;
		height: 40px;
		background: $gray-10;
		border-radius: 6px;
		display: flex;
		justify-content: center;
		align-items: center;

		img {
			border-radius: 6px;
			width: 40px;
			height: 40px;
			object-fit: cover;
		}

		iconify-icon {
			font-size: 28px;
		}
	}
	&-title {
		font-weight: 500;
		font-size: 14px;
		color: $dark-10;
		display: -webkit-box;
		-webkit-line-clamp: 1;
		-webkit-box-orient: vertical;
		overflow: hidden;
		line-height: 17px;

		@include breakpoint(lg) {
			font-size: 12px;
		}
	}

	&-desc {
		font-weight: 500;
		font-size: 12px;
		color: $gray-20;

		@include breakpoint(lg) {
			font-size: 10px;
		}
	}
	&-item {
		display: flex;
		align-items: center;
		gap: 12px;

		&:not(:last-child) {
			margin-bottom: 16px;
		}
	}
}
/* Alert */
.alert {
	border-radius: 6px;

	@include breakpoint(xl) {
		font-size: 14px;
	}

	@include breakpoint(xs) {
		font-size: 12px;
	}

	&-emerald {
		background-color: rgba($emerald, $alpha: 0.08);
		color: $emerald;

		.alert-close {
			background-color: rgba($emerald, $alpha: 0.16);
			iconify-icon {
				color: $emerald;
			}
		}
	}
	&-blue {
		background-color: rgba($blue-20, $alpha: 0.08);
		color: $blue-20;

		.alert-close {
			background-color: rgba($blue-20, $alpha: 0.16);
			iconify-icon {
				color: $blue-20;
			}
		}
	}
	&-red {
		background-color: rgba($red, $alpha: 0.08);
		color: $red;

		.alert-close {
			background-color: rgba($red, $alpha: 0.16);
			iconify-icon {
				color: $red;
			}
		}
	}
	&-fuchia {
		background-color: rgba($fuchia, $alpha: 0.08);
		color: $fuchia;

		.alert-close {
			background-color: rgba($fuchia, $alpha: 0.16);
			iconify-icon {
				color: $fuchia;
			}
		}
	}
	&-amber {
		background-color: rgba($amber, $alpha: 0.08);
		color: $amber;

		.alert-close {
			background-color: rgba($amber, $alpha: 0.16);
			iconify-icon {
				color: $amber;
			}
		}
	}

	&-close {
		position: absolute;
		right: 12px;
		top: 0;
		bottom: 0;
		margin: auto;
		border-radius: 6px;
		width: 32px;
		height: 32px;
		display: flex;
		justify-content: center;
		align-items: center;

		@include breakpoint(xs) {
			width: 26px;
			height: 26px;
		}

		iconify-icon {
			font-size: 24px;
			@include breakpoint(xs) {
				font-size: 20px;
			}
		}
	}
}
.card-team-profile {
	border: 1px solid $gray-40;
	border-radius: 12px;
	display: flex;
	align-items: center;
	gap: 14px;
	padding: 15px 18px;
	position: relative;

	@include breakpoint(lg) {
		border-radius: 6px;
	}

	@include breakpoint(md) {
		background-color: $white;
		border-color: $white;
	}

	@include breakpoint(xssm) {
		flex-direction: column;
		text-align: center;
	}

	&:not(:last-child) {
		margin-bottom: 12px;

		@include breakpoint(md) {
			margin-bottom: 0;
		}
	}

	&-group.custom-scroll {
		min-height: 600px;

		@include breakpoint(lg) {
			min-height: 346px;
		}
	}

	.avatar-wrapper {
		position: relative;

		span {
			position: absolute;
			top: 0;
			right: 0;
		}
	}

	.dropdown {
		position: absolute;
		top: 16px;
		right: 16px;
	}

	.envelop-icon {
		display: none;

		@include breakpoint(lg) {
			display: inline-flex;
			position: absolute;
			top: 14px;
			right: 34px;
		}

		@include breakpoint(xssm) {
			right: auto;
			left: 16px;
		}
	}

	&-avatar {
		text-align: center;

		.avatar-wrapper {
			position: relative;
			display: inline-block;

			img {
				overflow: hidden;
				border-radius: 100px;
				width: 62px;
				height: 62px;

				@include breakpoint(3xl) {
					width: 56px;
					height: 56px;
				}

				@include breakpoint(lg) {
					width: 42px;
					height: 42px;
				}
			}

			span {
				position: absolute;
				width: 24px;
				height: 24px;
				background-color: $white;
				border-radius: 100px;
				display: flex;
				justify-content: center;
				align-items: center;
				top: -2px;
				right: -4px;

				@include breakpoint(lg) {
					width: 18px;
					height: 18px;
				}

				iconify-icon {
					font-size: 18px;

					@include breakpoint(lg) {
						font-size: 14px;
					}
				}
			}
		}
	}
	&-content {
		h6 {
			font-weight: 600;
			font-size: 14px;
			color: $dark-10;
			margin-bottom: 4px;

			@include breakpoint(lg) {
				margin-bottom: -4px;
			}
			@include breakpoint(xssm) {
				margin-bottom: 4px;
			}
		}
		p {
			font-weight: 400;
			font-size: 12px;
			color: $dark-10;
			margin-bottom: 0;

			@include breakpoint(lg) {
				display: none;
			}
		}
		span {
			display: inline-block;
			font-weight: 500;
			font-size: 12px;
			border-radius: 100px;

			@include breakpoint(xxl) {
				font-size: 10px;
			}

			&.leader,
			&.admin {
				color: $emerald;
				background: transparent;

				@include breakpoint(xssm) {
					text-align: center;
					color: $emerald;
					background: $gray-90;
					padding: 3px 8px;
					margin-top: 2px;
				}
			}

			&.member {
				color: $blue-20;
				background: transparent;

				@include breakpoint(xssm) {
					text-align: center;
					color: $blue-20;
					background: $blue-10;
					padding: 3px 8px;
					margin-top: 2px;
				}
			}
		}
	}
}
/* Search Modal */
.modal-search {
	.modal-content {
		padding: 42px 0;

		@include breakpoint(sm) {
			padding: 24px 0;
		}
	}

	iconify-icon.search {
		font-size: 26px;
		margin-right: 6px;

		@include breakpoint(xl) {
			font-size: 22px;
		}
		@include breakpoint(lg) {
			font-size: 18px;
		}
		// @include breakpoint(md) {
		// 	font-size: 16px;
		// }
		// @include breakpoint(sm) {
		// 	font-size: 14px;
		// }
	}

	iconify-icon.clear {
		font-size: 24px;
		color: $purple-10;
		margin: 0;

		@include breakpoint(xl) {
			font-size: 22px;
		}
		// @include breakpoint(lg) {
		// 	font-size: 18px;
		// }
	}

	input {
		color: $dark-10;
		font-weight: 500;
		font-size: 26px;
		border: 0;

		@include breakpoint(xl) {
			font-size: 22px;
		}
		@include breakpoint(lg) {
			font-size: 18px;
		}
		@include breakpoint(md) {
			font-size: 16px;
		}
		@include breakpoint(sm) {
			font-size: 14px;
		}

		&::placeholder {
			color: $gray-20;
			font-size: 26px;

			@include breakpoint(xl) {
				font-size: 22px;
			}
			@include breakpoint(lg) {
				font-size: 18px;
			}
			@include breakpoint(md) {
				font-size: 16px;
			}
			@include breakpoint(sm) {
				font-size: 14px;
			}
		}

		&:focus,
		&:active {
			box-shadow: none;
		}
	}

	button[type="reset"] {
		padding: 7px 14px;
		border-radius: 100px;
		border-radius: 32px;
		background: rgba($color: $purple-10, $alpha: 0.12);
		color: $purple-10;
		font-weight: 500;
		font-size: 14px;
		display: flex;
		align-items: center;

		@include breakpoint(sm) {
			padding: 4px 10px;
		}

		&:hover {
			background: rgba($color: $purple-10, $alpha: 0.12);
			color: $purple-10;
		}

		span {
			display: block;
			line-height: 1;

			@include breakpoint(md) {
				font-size: 14px;
			}
			@include breakpoint(sm) {
				font-size: 12px;
			}
		}
	}
	.search-input {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 0 42px;

		@include breakpoint(sm) {
			margin: 0 24px;
		}
	}

	&-footer {
		display: flex;
		align-items: center;
		justify-content: flex-end;
		margin: 34px 42px 0;
		font-size: 14px;

		@include breakpoint(sm) {
			margin: 34px 24px 0;
		}
		@include breakpoint(xs) {
			margin: 20px 24px 0;
		}
	}
}
.modal-tabs {
	.nav-tabs {
		padding: 0 42px;
		margin-top: 26px;
		border-bottom-width: 2px;
		gap: 12px;

		@include breakpoint(sm) {
			padding: 0 24px;
			gap: 8px;
		}
		@include breakpoint(xs) {
			gap: 4px;
		}
	}
	.nav-link {
		padding-bottom: 14px;
		color: $gray-20;
		font-weight: 600;
		border-width: 2px;
		margin-bottom: -2px;
		border-top: 0;
		border-left: 0;
		border-right: 0;

		&:hover,
		&:focus,
		&:focus-visible {
			border-color: transparent;
		}

		&.active {
			border-bottom-color: $purple-10;
			color: $purple-10;
		}

		@include breakpoint(md) {
			font-size: 14px;
		}
		@include breakpoint(xssm) {
			padding-bottom: 10px;
			padding-left: 12px;
			padding-right: 12px;
		}
		@include breakpoint(xs) {
			font-size: 12px;
		}
	}

	+ {
		.tab-content {
			margin: 36px 42px 0;

			@include breakpoint(sm) {
				margin: 36px 24px 0;
			}

			@include breakpoint(xssm) {
				margin: 28px 24px 0;
			}
		}
	}
}
.modal-xxl {
	max-width: 1200px;

	@include breakpoint(xl) {
		margin: 16px;
	}
}

/* Client Information */
.client-profile-information {
	display: flex;
	align-items: center;

	&-item {
		&:not(:last-child) {
			margin-right: 32px;

			@include breakpoint(md) {
				margin-right: 22px;
			}
		}

		display: flex;
		align-items: center;
		gap: 8px;
	}
	&-icon {
		width: 32px;
		height: 32px;
		border: 1px solid $gray-40;
		border-radius: 6px;
		display: flex;
		justify-content: center;
		align-items: center;

		@include breakpoint(md) {
			width: 25px;
			height: 25px;
		}
		iconify-icon {
			font-size: 18px;
			color: $purple-10;

			@include breakpoint(md) {
				font-size: 16px;
			}
		}
	}
	&-text {
		font-weight: 500;
		font-size: 14px;
		color: $dark-10;

		@include breakpoint(lg) {
			font-size: 12px;
		}
	}
}
.task-list {
	display: flex;
	gap: 10px;
	align-items: center;

	&:first-child {
		margin-top: 16px;
	}

	&:not(:last-child) {
		margin-bottom: 16px;
	}

	&-item {
		background-color: $white;
		border: 1px solid $gray-500;
		border-radius: 6px;
		padding: 14px 20px;
		width: 100%;
		font-size: 14px;
		transition: 0.4s all ease;

		@include breakpoint(sm) {
			font-size: 12px;
			padding: 12px 16px;
		}

		span {
			font-size: 14px;

			@include breakpoint(md) {
				font-size: 12px;
			}
		}
	}

	iconify-icon.delete {
		font-size: 24px;
		color: $gray-20;
		transition: 0.4s all ease;

		&:hover {
			cursor: pointer;
			color: $red;
		}
	}
}
.member-list {
	&-item {
		background: $gray-100;
		border: 1px solid $gray-40;
		border-radius: 50px;
		padding: 4px 8px;
		display: inline-flex;
		align-items: center;
		gap: 4px;

		img {
			width: 26px;
			height: 26px;
			border-radius: 100px;
		}

		p {
			font-weight: 500;
			font-size: 12px;
			color: $dark-10;
		}
	}

	&-group {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		gap: 10px;
		flex-wrap: wrap;
	}

	&-wrapper {
		display: flex;
		align-items: center;
		gap: 12px;
		justify-content: space-between;

		.add {
			border: 1px solid $gray-500;
			border-radius: 100px;
			width: 40px;
			height: 40px;
			display: flex;
			justify-content: center;
			align-items: center;
			flex: 0 0 auto;

			&:hover {
				cursor: pointer;
			}

			@include breakpoint(sm) {
				width: 32px;
				height: 32px;
			}
		}

		iconify-icon.add-member {
			font-size: 20px;
			color: $gray-20;

			@include breakpoint(xssm) {
				font-size: 18px;
			}
		}
	}

	&-item {
		iconify-icon.delete {
			font-size: 18px;
			color: $gray-20;
			margin-left: 4px;

			&:hover {
				cursor: pointer;
			}
		}
	}
}
